.hero {
  margin-top: 50px;
  margin-bottom: 50px;
  padding: 50px 0 42px;
  background-image: url('./../img/hero.webp');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;

  @media (min-width: 1920px) {
    background-image: url('./../img/hero-2x.webp');
  }

  @media (max-width: 900px) {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: #192b6d;
    opacity: 0.8;
  }

  &__logo {
    display: block;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 30px;

    width: 60px;
    height: 40px;

    @include mobile {
      margin-bottom: 20px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__content {
    position: relative;
    z-index: 4;
    max-width: 648px;
    margin: 0 auto;
  }

  &__title {
    margin: 0;
    margin-bottom: 50px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 64px;
    text-align: center;
    color: #fff;

    @media (max-width: 1000px) {
      font-size: 50px;
      margin-bottom: 30px;
    }

    @media (max-width: 850px) {
      font-size: 42px;
    }

    @include mobile {
      font-size: 36px;
      margin-bottom: 20px;
    }
  }

  &__desc {
    margin: 0;
    margin-bottom: 75px;
    font-family: var(--font4);
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    text-align: center;
    color: #fff;

    @media (max-width: 850px) {
      font-size: 16px;
    }
  }

  &__items {
    display: flex;
    gap: 20px;
    justify-content: space-between;
  }

  &__item {
    flex: 1;
  }
}

.hero-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 20px;

  @media (max-width: 850px) {
    gap: 10px;
  }

  &__icon {
    width: 49px;
    height: 49px;

    @media (max-width: 850px) {
      width: 35px;
      height: 35px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__capture {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 26px;
    text-align: center;
    color: #fff;

    @media (max-width: 850px) {
      font-size: 20px;
    }

    @include mobile {
      font-size: 14px;
    }
  }
}